<template>
  <div class="classifyManagement">
    <div class="mid mescroll">
      <div class="list">
        <div class="item" v-for="(item,index) in cateList" :key="index">
          <van-swipe-cell>
            <div class="classify">
              <div class="name">{{item.cateName}}</div>
              <div class="num">{{item.treeSort}}</div>
              <van-icon name="arrow" />
            </div>
            <template slot="right">
              <van-button square type="primary" text="编辑" @click="edit(item,index)" />
              <van-button square type="danger" text="删除" @click="del(item,index)" />
            </template>
          </van-swipe-cell>
        </div>
      </div>
    </div>
    <div class="bottom" @click="add()">新增分类</div>
    <van-popup v-model="show">
      <div class="popup">
        <div class="popup-mid">
          <p class="title">{{type == '1'?'新增商品分类':'编辑商品分类'}}</p>
          <van-cell-group>
            <van-field v-model="detail.cateName" input-align="right" clearable label="分类名" placeholder="请输入商品分类名称" @blur="blur()" />
            <van-field v-model="detail.treeSort" input-align="right" clearable type="number" label="排序" placeholder="默认99" @blur="blur()" />
          </van-cell-group>
        </div>
        <div class="popup-bottom">
          <div class="btn" @click="submit">提交</div>
        </div>
      </div>
    </van-popup>
  </div>
</template>
<script>
import { SwipeCell, Button, Popup, CellGroup, Field, Dialog, Icon } from "vant";
import { getShopCateList, editShopCate, delShopCate } from "@/api";
export default {
  components: {
    [SwipeCell.name]: SwipeCell,
    [Button.name]: Button,
    [Popup.name]: Popup,
    [CellGroup.name]: CellGroup,
    [Field.name]: Field,
    [Icon.name]: Icon
  },
  data() {
    return {
      cateList: [],
      index: null,
      show: false,
      name: "",
      detail: {
        cateName: "",
        treeSort: ""
      },
      type: "1"
    };
  },
  methods: {
    blur() {
      document.body.scrollTop = 0;
    },
    edit(item, index) {
      this.show = true;
      this.type = "2";
      this.detail = Object.assign({}, item);
    },
    submit() {
      if (!this.detail.cateName) {
        this.$toast("请输入分类名称");
        return;
      }
      if (!this.detail.treeSort) {
        this.$toast("请输入排序");
        return;
      }
      const loading = this.$toast.loading({
        duration: 0, // 持续展示 toast
        forbidClick: true // 禁用背景点击
      });
      editShopCate({
        id: this.detail.id || undefined,
        cateName: this.detail.cateName,
        treeSort: this.detail.treeSort
      }).then(res => {
        loading.clear();
        if (res.code == 0) {
          this.show = false;
          this.$toast(this.type == "1" ? "新增成功" : "修改成功");
          this.loadData();
        } else {
          this.$toast(res.msg);
        }
      });
    },
    del(item, index) {
      let that = this;
      Dialog.confirm({
        title: "标题",
        message: "是否确认删除此分类？"
      })
        .then(() => {
          // on confirm
          const loading = this.$toast.loading({
            duration: 0, // 持续展示 toast
            forbidClick: true // 禁用背景点击
          });
          delShopCate({ id: item.id }).then(res => {
            loading.clear();
            if (res.code == 0) {
              that.$toast("删除成功");
              that.loadData();
            } else {
              that.$toast(res.msg);
            }
          });
        })
        .catch(() => {
          // on cancel
        });
    },
    add() {
      this.show = true;
      this.type = "1";
      this.detail = {
        cateName: "",
        treeSort: ""
      };
    },
    loadData() {
      const loading = this.$toast.loading({
        duration: 0, // 持续展示 toast
        forbidClick: true // 禁用背景点击
      });
      getShopCateList().then(res => {
        loading.clear();
        if (res.code == 0) {
          this.cateList = res.data;
        } else {
          this.$toast(res.msg);
        }
      });
    }
  },
  mounted() {
    document.title = "分类管理";
    this.loadData();
  }
};
</script>
<style lang="scss" scoped>
.classifyManagement {
  .mid {
    height: calc(100vh - 50px);
    .list {
      .item {
        background: #fff;
        border-bottom: 1pt solid #efefef;
        .classify {
          padding: 10px;
          display: flex;
          align-items: center;
          line-height: 16px;
          .name {
            flex: 2;
            font-weight: bold;
            line-height: 30px;
          }
          .num {
            flex: 1;
            text-align: right;
            color: #999;
            font-size: 13px;
            margin-right: 5px;
          }
        }
        .van-button {
          height: 100%;
        }
      }
    }
  }
  .bottom {
    position: fixed;
    left: 0;
    bottom: 0;
    width: 100vw;
    height: 50px;
    line-height: 50px;
    background: $base-color;
    color: #fff;
    text-align: center;
  }
  .van-popup {
    border-radius: 5px;
  }
  .popup {
    background: #fff;
    width: 85vw;
    height: 55vh;
    position: relative;
    &-mid {
      .title {
        line-height: 50px;
        text-align: center;
      }
      .van-cell {
        padding: 10px;
      }
    }
    &-bottom {
      padding: 10px;
      position: absolute;
      width: calc(100% - 20px);
      height: 45px;
      bottom: 0;
      left: 0;
      .btn {
        width: 100%;
        line-height: 45px;
        text-align: center;
        background: #ee0a24;
        color: #fff;
        font-size: 13px;
        border-radius: 5px;
      }
    }
  }
}
</style>